@charset "UTF-8";
* {
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

body {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #767e9e;
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0; }

p {
  text-align: justify; }

h2 {
  font-size: 30px;
  font-weight: 900; }

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 41px; }

.header {
  border-bottom: 6px solid #323746;
  background-color: #2d303a;
  text-align: center;
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 5; }

.header__wrapper {
  height: 89px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: height 0.3s; }

.header__wrapper_small {
  height: 50px; }

.logo {
  font-size: 25px;
  font-weight: 700; }

.logo a {
  text-decoration: none;
  color: #fff; }

.logo__star {
  color: #f06c64; }

.nav {
  padding-top: 10px; }

.nav__list {
  list-style: none; }

.nav__item {
  display: inline-block;
  position: relative; }

.nav__item + .nav__item {
  margin-left: 26px; }

.nav__item + .nav__item::before {
  content: 'Â·';
  color: #494e62;
  position: absolute;
  top: 50%;
  left: -15px;
  transform: translateY(-50%); }

.nav__item_active .nav__link {
  color: #f06c64; }

.nav__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  transition: color .3s; }

.nav__link:hover {
  color: #f06c64; }

.slider {
  background-color: #f06c64;
  border-bottom: 6px solid #ea676b; }

.slider__wrapper {
  position: relative;
  padding: 53px 0 58px;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.slider-item {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.phone-vertical {
  margin-left: 74px; }

.btn-left,
.btn-right {
  cursor: pointer;
  position: absolute;
  width: 16px;
  height: 26px;
  top: 49.5%;
  transform: translateY(-50%);
  opacity: 0.2;
  transition: opacity 0.3s; }

.btn-left:hover,
.btn-right:hover {
  opacity: 1; }

.btn-left {
  left: 2px;
  background-image: url(ce2c61d07799bc13076db001808ff550.png); }

.btn-right {
  right: 0;
  background-image: url(377593fb71c042d448fc40b830045374.png); }

.services {
  background-color: #f2f2f2;
  padding: 60px 0 33px;
  border-bottom: 6px solid #ffffff; }

.services__title {
  margin-bottom: 17px;
  color: #666d89; }

.services__desc {
  margin-bottom: 44px;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px; }

.layout-3-col {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.layout-3-col > * {
  flex-basis: 32%; }

.layout-4-col {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.layout-4-col > * {
  flex-basis: 21%; }

.services-item {
  width: 300px;
  display: flex;
  margin-bottom: 27px;
  max-height: 100px;
  height: 100%;
  overflow: hidden;
  word-wrap: break-word; }

.services-item__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3px;
  margin-right: 22px;
  min-width: 59px;
  height: 59px;
  border: 2px solid #666d89;
  border-radius: 50%; }

.services-item__content h3 {
  font-size: 18px;
  font-weight: 900;
  margin-bottom: 7px; }

.services-item__content p {
  line-height: 22px;
  font-weight: 400; }

.portfolio {
  background-color: #2d303a;
  padding: 60px 0 66px;
  border-bottom: 6px solid #323746; }

.portfolio__title {
  margin-bottom: 23px; }

.portfolio__desc {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 14px; }

.tags {
  display: inline-flex;
  margin-bottom: 18px; }

.tags__item + .tags__item {
  margin-left: 9px; }

.tag {
  cursor: pointer;
  padding: 0 6px;
  line-height: 21px;
  border: 1px solid #666d89;
  border-radius: 5px;
  transition: all 0.3s; }

.tag:hover {
  border-color: #fff;
  color: #fff; }

.tag_active {
  padding: 0 8px;
  border-color: #fff;
  color: #fff; }

.portfolios {
  height: 613px;
  overflow: hidden; }

.portfolios img {
  display: block; }

.portfolios__item {
  margin-bottom: 20px;
  width: 220px;
  height: 187px; }

.portfolios__item_active {
  outline: 5px solid #F06C64;
  outline-offset: -5px; }

.aboutus {
  background-color: #f2f2f2;
  padding: 59px 0 64px;
  border-bottom: 6px solid #ffffff; }

.aboutus__title {
  margin-bottom: 18px; }

.aboutus__desc {
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
  margin-bottom: 45px; }

.staffs__img {
  margin-bottom: 12px; }

.staffs__name {
  max-width: 300px;
  font-size: 18px;
  font-weight: 900;
  color: #666d89;
  margin-bottom: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.staffs__descr {
  line-height: 21px;
  margin-bottom: 23px; }

.quote {
  background-color: #f06c64;
  padding: 59px 0 63px;
  border-bottom: 6px solid #ea676b;
  color: #f0d8d9; }

.quote__title {
  color: #f0d8d9;
  margin-bottom: 18px; }

.quote__text {
  font-size: 18px;
  line-height: 30px;
  font-weight: 300;
  margin-bottom: 45px; }

.quote__box {
  display: inline-flex; }

.quote__form {
  min-width: 620px;
  width: 100%;
  margin-right: 20px; }

.quote__form input,
.quote__form textarea {
  width: 100%;
  resize: none;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 16px;
  background-color: #d6564f;
  font-size: 12px;
  height: 38px;
  color: #f0d8d9; }

.quote__form input::placeholder,
.quote__form textarea::placeholder {
  font-size: 12px;
  color: #f48c8f; }

.quote__form textarea::placeholder {
  letter-spacing: normal; }

.quote__form textarea {
  height: 202px;
  margin-bottom: 15px; }

.quote__form input {
  margin-bottom: 16px; }

.quote__form button {
  border: none;
  background-color: #d6564f;
  color: #f0d8d9;
  padding: 10px 15px;
  border-radius: 5px; }

.quote__contact {
  width: 100%; }

.contact-inf__title {
  font-size: 18px;
  line-height: 18px;
  font-weight: 900;
  margin-bottom: 9px; }

.contact-inf__text {
  line-height: 22px;
  margin-bottom: 22px; }

.contact-inf__link a {
  color: #f0d8d9;
  text-decoration: none;
  margin-left: 10px; }

.contact-inf__link + div {
  margin-top: 7px; }

.icon-contact {
  display: inline-block;
  width: 13px;
  height: 12px;
  background-repeat: no-repeat;
  background-position: center bottom; }

.icon-contact_map {
  background-image: url(0bef0740af5770883a315bc44215c956.png); }

.icon-contact_phone {
  background-image: url(068dca42312b5c958b95eea95bae67c5.png); }

.icon-contact_mail {
  background-image: url(8f2a9b57ba7f8b2544c80cf40263bac9.png); }

.footer {
  background-color: #2d303a; }

.footer__wrapper {
  height: 76px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer__copy {
  margin-left: 3px; }

.footer__copy span {
  display: inline-block;
  margin-right: 2px; }

.social {
  display: inline-flex; }

.social a + a {
  margin-left: 8px; }

.ico {
  width: 32px;
  height: 32px;
  border: 1px solid #69708b;
  border-radius: 50%;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center center; }

.ico_fb {
  background-image: url(fff7f0fb40aaf981edae5254e1a24eb4.png); }

.ico_google {
  background-image: url(94c5ea156e744bb11947e4035487aed6.png); }

.ico_linkedin {
  background-image: url(d4e0de5664a263e61649b86580e8953e.png); }

.ico_twitter {
  background-image: url(c1acc17be515553ed4473073a5d65b01.png); }

.overlay {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 6;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center; }

.modal {
  min-width: 350px;
  min-height: 150px;
  background-color: #fff;
  padding: 10px 15px; }

.modal__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  border-bottom: 1px solid #99a0c6; }

.modal__title {
  font-size: 20px;
  font-weight: bold; }

.modal__content {
  margin-bottom: 15px;
  border-bottom: 1px solid #99a0c6;
  font-size: 16px; }

.modal__text {
  margin-bottom: 5px; }

.modal__footer {
  display: flex;
  justify-content: flex-end; }

.modal__btn button {
  border: 1px solid white;
  cursor: pointer;
  padding: 5px 10px;
  color: #fff;
  background-color: #99a0c6;
  outline: none; }
